: -1.5em;\n}\n\n.content .item .imagePlain {\n\t/* and override content pane's margin always: */\n\tmargin-right: -0.5em;\n\tmargin-left: -0.5em;\n}\n\n.content img {\n\tmax-width: 100%;\n}\n\n.item .touchable {\n\tcursor: pointer;\n\tposition: relative;\n\tdisplay: inline-block;\n\tmax-width: 100%;\n\tmax-height: 100%;\n}\n\n.item .touchable img {\n\tmax-width: 100%;\n\tmax-height: 100%;\n}\n\n.item .touchableIndicator {\n\topacity: 0;\n\ttransition: opacity 1.5s;\n\tposition: absolute;\n\tbottom: 7px;\n\tright: 5px;\n\tfont-family: Story;\n\tfont-size: 1em;\n\tpointer-events: none;\n\t/* let clicks pass through to the image */\n}\n\n.touchableIndicator:before {\n\tcontent: \"\\e609\";\n\ttext-shadow: 1px 1px 1px #FFF;\n}\n\n.item .touchable:hover .touchableIndicator {\n\topacity: 0.8;\n}\n\n\n/*============================ ============================ ============================\n  inline item detail */\n.itemDetail.ng-enter,\n.itemDetail.ng-leave {\n\ttransition: 1s all;\n\toverflow: hidden;\n}\n\n.itemDetail.ng-enter {\n\theight: 1px;\n\topacity: 0;\n}\n\n.itemDetail.ng-enter-active {\n\theight: 300px;\n\topacity: 1;\n}\n\n.itemDetail.ng-leave {\n\theight: 300px;\n\topacity: 1;\n}\n\n.itemDetail.ng-leave-active {\n\theight: 1px;\n\topacity: 0;\n}\n\n\n/*============================ ============================ ============================\n  Modal overlay */\n\n.modal {\n\ttransition: 1s all;\n\tmax-height: calc(100% - 100px);\n\tposition: fixed;\n\twidth: 75%;\n\ttop: 30%;\n\tleft: 50%;\n\theight: 80%;\n\ttransform: translate(-50%, -40%);\n}\n\n.modal.ng-enter,\n.modal.ng-leave.ng-leave-active {\n\tleft: 49%;\n\tright: 49%;\n\ttop: 33%;\n\toverflow: hidden;\n}\n\n.modal.ng-enter-active,\n.modal.ng-leave {\n\tleft: 5%;\n\tright: 5%;\n\ttop: 10%;\n\tdisplay: block !important;\n\toverflow: hidden;\n}\n\n.modalmenu {\n\tborder-radius: 1em 1em 0 0;\n\ttext-align: right;\n\tpadding: 0 1em;\n\tcolor: #FFF;\n\tbackground-color: #000;\n\theight: 30px;\n\tline-height: 30px\n}\n\n.modalmenu a,\n.modalmenu a:before {\n\tcolor: #FFF !important;\n}\n\n.modalimage {\n\ttext-align: center;\n\t/* width and height are needed for IE, which otherwise borks exit animation: see TS-847 */\n\twidth: 100%;\n\theight: 100%;\n}\n\n.modalimage img {\n\tmax-width: 100%;\n\tbox-shadow: 3px 3px 15px rgba(0, 0, 0, 0.9);\n\tbackground-color: #FFF;\n}\n\n\n/*============================ ============================ ============================\n *\n * timestamps\n\n\tnormal:                     item: pad 60px, timestamp -60px\n\tnormal, timestampInline:    item: pad 0,    timestamp 0 inline;\n\tnormal, timestampNone:      item: pad 0,    timestamp display none\n  normal, headerItem:         item: -60px,    timestamp display none;\n  normal, sidebar:            item: pad 0,    timestamp display none;\n\n\tnarrow:                     item: pad 0,    timestamp 0 inline;\n\tnarrow, timestampInline:    item: pad 0,    timestamp 0 inline;\n\tnarrow, timestampNone:      item: pad 0,    timestamp display none;\n  normal, headerItem:         item: pad 0,    timestamp display none;\n  narrow, sidebar:            item: pad 0,    timestamp display none;\n\n=========================== */\n\n.content .item {\n\tpadding-left: 60px;\n}\n\n.content .item .startTime {\n\tmargin-left: -55px;\n}\n\n.searchMode .content .item .sxs-item .startTime {\n\t/* these items have 10px padding */\n\tmargin-left: -65px;\n}\n\n.content .item .headerItem {\n\tmargin-left: -50px;\n}\n\n.content .item.timestampInline,\n.content .item.timestampNone,\n.content .item.sidebarL,\n.content .item.sidebarR {\n\tpadding-left: 0.5em;\n}\n\n.content.isNarrow .item,\n.content.isNarrow .item.timestampInline,\n.content.isNarrow .item.timestampNone,\n.content.isNarrow .item.sidebarL,\n.content.isNarrow .item.sidebarR {\n\tpadding-left: 0.5em;\n}\n\n.content.isNarrow .item.timestampNone .startTime,\n.content.isNarrow .item.headerItem .startTime,\n.content.isNarrow .item.sidebarL .startTime,\n.content.isNarrow .item.sidebarR .startTime {\n\tdisplay: none;\n}\n\n\n/* =========================== */\n\n.item.timestampNone .startTime {\n\tdisplay: none\n}\n\n\n/* =========================== */\n\n.content.isNarrow .item .headerItem,\n.timestampNone .headerItem,\n.timestampInline .headerItem {\n\tmargin-left: 0 !important;\n}\n\n.content.isNarrow .item .startTime,\n.content.isNarrow .item .sxs-item .startTime,\n.item.timestampInline .startTime {\n\tdisplay: inline;\n\tmargin-left: 0;\n\twidth: auto;\n\ttext-align: left;\n\tmargin-right: 0.4em;\n}\n\n.content.isNarrow .item .pullquote:before {\n\tdisplay: inline;\n\tmargin-left: 0;\n\twidth: auto;\n\ttext-align: left;\n\theight: 1px;\n\tpadding-right: 5px;\n}\n\n\n/* =========================== */\n\n.item.timestampSidebar .startTime {\n\ttransform: rotate(90deg) translateX(100%);\n\ttransform-origin: 100% 0;\n\twidth: auto;\n\tfont-size: 20px;\n\tmargin-left: -45px;\n}\n\n\n/* =========================== */\n\n.item.timestampSmall .displayTime {\n\tfont-size: 0.86em;\n}\n\n\n/* =========================== */\n\n\n/*============================ ============================ ============================\n *\n * Template variants\n *\n//=========================== */\n\n.item .pullquote:before {\n\tcolor: rgba(0, 0, 0, 0.1);\n\tcontent: \"“\";\n\tfloat: left;\n\tfont-size: 2.5em;\n\tline-height: 0.8em;\n\ttext-align: right;\n\twidth: 50px;\n\ttext-shadow: none !important;\n\theight: 1px;\n}\n\n.item .pullquote {\n\tfont-size: 2em;\n\tpadding-top: 0.3em;\n\tline-height: 1.1em;\n}\n\n.item .pullquoteSpeaker {\n\ttext-align: right;\n\tfont-style: italic;\n\tfont-size: 0.8em\n}\n\n/*now placing emdash in HTML*/\n/*.item .pullquoteSpeaker:before {*/\n\t/*content: \" — \";*/\n/*}*/\n\n\n/* - - - - - - - - - - - - - -  */\n\n.item .captionContainer {\n\tposition: relative;\n\toverflow: hidden;\n\twidth: 100%;\n}\n\n.item .caption {\n\tposition: absolute;\n\t/* TODO why is this not zero: */\n\tbottom: 4px;\n\tleft: 0;\n\tright: 0;\n\tpadding: 0.5em;\n\topacity: 1;\n\ttransition: 0.7s all;\n\tbackground-color: #333;\n\tbackground-color: rgba(0, 0, 0, 0.7);\n\tborder-top: 1px solid #666;\n\tcolor: #FFF;\n}\n\n.item .captionContainer img {\n\twidth: 100%;\n}\n\n.item.colorInvert .caption {\n\tbackground-color: #eee;\n\tbackground-color: rgba(255, 255, 255, 0.7);\n\tcolor: #000;\n\tborder-top: 1px solid #666;\n}\n\n.item:hover .caption {\n\topacity: 0;\n}\n\n.item .slidingcaption {\n\ttransform: translateY(0);\n}\n\n.item:hover .slidingcaption {\n\ttransform: translateY(100%);\n}\n\n\n/* - - - - - - - - - - - - - -  */\n\n.item .thumbnail img {\n\twidth: 100%;\n}\n\n.item .thumbnail {\n\tmax-width: 72px;\n\ttransition: all 0.7s;\n\tfloat: left;\n\tmargin-right: 1em;\n}\n\n.item .thumbnail.fullsize {\n\tmax-width: 100%;\n\tfloat: none;\n}\n\n\n/* - - - - - - - - - - - - - -  */\n\n.item .question label {\n\tdisplay: block;\n\tpadding: 0.3em;\n}\n\n.item .question .correct {\n\tborder-left: 5px solid #0C0;\n}\n\n.item .question .incorrect {\n\tborder-left: 5px solid #C00;\n}\n\n.item .question input[type=\"radio\"] {\n\tmargin-right: 0.5em;\n}\n\n.item .flotContainer {\n\tposition: relative;\n}\n\n.item .flotContainer:after {\n\tcontent: \"\";\n\tclear: both;\n\tdisplay: block;\n}\n\n.item .chartContainer {\n\twidth: 50%;\n\tmax-width: 400px;\n\tfloat: left;\n}\n\n.item .legendContainer {\n\tfloat: right;\n\twidth: 50%;\n}\n\n.item .legendContainer td {\n\tpadding: 2px;\n\tvertical-align: top;\n}\n\n.isNarrow .item .chartContainer {\n\twidth: auto;\n\tmax-width: auto;\n\tmargin: auto;\n\tfloat: none;\n}\n\n.isNarrow .item .legendContainer {\n\tfloat: none;\n\tmax-width: 100%;\n}\n\n\n/* - - - - - - - - - - - - - -  */\n\n.item .definition .itemHead div {\n\tpadding-left: 40px;\n\tbackground: url('/images/definition-1.png') top left no-repeat;\n}\n\n.item .definition .itemHead div span {\n\tfont-size: 1.6em;\n\tfont-weight: 700;\n\tline-height: 1.6em;\n\tborder-bottom: 1px solid;\n}\n\n.item .definition .text {\n\tpadding-left: 40px;\n\tfont-size: 1.3em;\n\tbackground: url('/images/definition-2.png') top left no-repeat;\n}\n\n","/*\n\tExtra styling for 'current' items.\n\tFor now, avoid adding colors here, instead add images or rgba colors to modify existing background.\n\t(Once we have a solidified set of options in colors.css, may add matching variants for each of them here instead.)\n\n*/\n\n.reviewMode .item.isCurrent {\n\tbackground-color: rgba(0, 0, 0, 0.2);\n}\n.colorInvert .reviewMode .item.isCurrent {\n\tbackground-color: rgba(255, 255, 255, 0.3);\n}\n/* - - - - - - */\n/*disable transition for showCurrent scenes*/\n.content.allowHighlights .item {\n\ttransition: none;\n}\n\n.content.allowHighlights .highlightSolid.item.isCurrent {\n\tbackground-color: rgba(0, 0, 0, 0.2);\n}\n.content.allowHighlights .highlightBorder.item.isCurrent {\n\tborder: 3px solid rgba(0, 0, 0, 0.3);\n}\n.content.allowHighlights .highlightSide.item.isCurrent {\n\tborder-left: 10px solid rgba(0, 0, 0, 0.3);\n\ttransition: all ease-in-out 1s;\n\t/*box-shadow: -10px 0 0 rgba(0, 0, 0, 0.3);*/\n}\n.content.allowHighlights .highlightBloom.item.isCurrent {\n\tbackground-color: rgba(255, 255, 200, 0.4);\n\tbox-shadow: 0 0 8px rgba(255, 255, 200, 0.9);\n}\n.content.allowHighlights .highlightHighlighter.item.isCurrent {\n\tbackground-image: url('/images/highlighter.png');\n\tbackground-size: 100% 100%;\n}\n.content.allowHighlights .highlightTilt.item.isCurrent {\n\ttransform: rotateY(-20deg) scale(1.18);\n\ttransform-origin: 70% 50% 0;\n\ttransform-style: preserve-3d;\n}\n","/* \n\ttransitionFade\n\ttransitionSlideL\n\ttransitionSlideR\n\ttransitionPop\n*/\n/* Misc useful keyframes */\n\n@keyframes fadeOut {\n\t0% {\n\t\topacity: 1;\n\t}\n\t100% {\n\t\topacity: 0;\n\t}\n}\n\n@keyframes fadeIn {\n\t0% {\n\t\topacity: 0;\n\t}\n\t100% {\n\t\topacity: 1;\n\t}\n}\n\n@keyframes delayedFadeOut {\n\t0% {\n\t\topacity: 1;\n\t}\n\t33% {\n\t\topacity: 1;\n\t}\n\t100% {\n\t\topacity: 0;\n\t}\n}\n\n@keyframes delayedFadeIn {\n\t0% {\n\t\topacity: 0;\n\t}\n\t33% {\n\t\topacity: 0;\n\t}\n\t100% {\n\t\topacity: 1;\n\t}\n}\n\nbody {\n\t/* Addresses a small issue in webkit: http://bit.ly/NEdoDq */\n\t\n\t-webkit-backface-visibility: hidden;\n}\n/* ------------------------------------------------------------------------------------------------------------------ */\n/* \nDefaults.  Always include a bit of height animation, to make it look less jumpy when items are removed from\nthe middle of a list\n\nTransition time should be set for the specific object type\n */\n/* disabling the height animation for now; looks bad in Safari. TODO fix in safari */\n\n.animate.ng-enter,\n.animate.ng-leave,\n.animate.ng-move {\n\ttransition: all ease-in-out 0.5s;\n}\n\n.animate.noTransitions {\n\ttransition: none !important;\n}\n\n.content .animate.ng-enter {\n\t/*max-height: 1px;*/\n}\n\n.content .animate.ng-enter.ng-enter-active {\n\t/*max-height: 300px;*/\n}\n\n.content .animate.ng-leave {\n\t/*max-height:100px;*/\n\t\n\tdisplay: block !important;\n}\n\n.content .animate.ng-leave.ng-leave-active {\n\t/*max-height:0;*/\n}\n/* ------------------------------------------------------------------------------------------------------------------\n   transitionFade */\n\n.animate.ng-enter.transitionFade {\n\topacity: 0;\n}\n\n.animate.ng-enter-active.transitionFade {\n\topacity: 1;\n}\n\n.animate.ng-leave.transitionFade {\n\topacity: 1;\n}\n\n.animate.ng-leave.ng-leave-active.transitionFade {\n\topacity: 0;\n}\n/* ------------------------------------------------------------------------------------------------------------------\n   transitionSlideL */\n\n.animate.ng-enter.transitionSlideL {\n\ttransform: translateX(50px);\n\topacity: 0;\n}\n\n.animate.ng-enter-active.transitionSlideL {\n\ttransform: translateX(0);\n\topacity: 1;\n}\n\n.animate.ng-leave.transitionSlideL {\n\ttransform: translateX(0);\n\topacity: 1;\n}\n\n.animate.ng-leave.ng-leave-active.transitionSlideL {\n\ttransform: translateX(-50px);\n\topacity: 0;\n}\n/* ------------------------------------------------------------------------------------------------------------------\n   transitionSlideR */\n\n.animate.ng-enter.transitionSlideR {\n\ttransform: translateX(-50px);\n\topacity: 0;\n}\n\n.animate.ng-enter-active.transitionSlideR {\n\ttransform: translateX(0);\n\topacity: 1;\n}\n\n.animate.ng-leave.transitionSlideR {\n\ttransform: translateX(0);\n\topacity: 1;\n}\n\n.animate.ng-leave.ng-leave-active.transitionSlideR {\n\ttransform: translateX(50px);\n\topacity: 0;\n}\n/* ------------------------------------------------------------------------------------------------------------------\n   transitionExpandW */\n\n.animate.transitionExpandW {\n\toverflow: hidden;\n\tdisplay: block;\n}\n\n.animate.ng-enter.transitionExpandW {\n\tmax-width: 1px;\n\topacity: 0;\n}\n\n.animate.ng-enter-active.transitionExpandW {\n\tmax-width: 1200px;\n\topacity: 1;\n}\n\n.animate.ng-leave.transitionExpandW {\n\tmax-width: 1200px;\n\topacity: 1;\n}\n\n.animate.ng-leave.ng-leave-active.transitionExpandW {\n\tmax-width: 1px;\n\topacity: 0;\n}\n/* ------------------------------------------------------------------------------------------------------------------\n   transitionPop */\n\n.animate.ng-enter.transitionPop {\n\ttransform: scale(0.9);\n\topacity: 0;\n}\n\n.animate.ng-enter-active.transitionPop {\n\ttransform: scale(1);\n\topacity: 1;\n}\n\n.animate.ng-leave.transitionPop {\n\ttransform: scale(1);\n\topacity: 1;\n}\n\n.animate.ng-leave.ng-leave-active.transitionPop {\n\ttransform: scale(0.9);\n\topacity: 0;\n}\n/* ------------------------------------------------------------------------------------------------------------------\n   transitionNone */\n\n.animate.ng-enter.transitionNone,\n.animate.ng-leave.transitionNone,\n.animate.ng-move.transitionNone {\n\ttransition: all ease-in-out 0s;\n}\n\n","/*============================\n *\n * Default\n *\n//===========================*/\n\nbody {\n\tfont-family: 'Ope